.skill-cardly1, .skill-cardly2, .skill-cardly3, .skill-cardly4, .skill-cardly5 {
    width: 18%;
    margin-inline: 1%;
}

@media (min-width:321px) and (max-width: 517px ) {
    .skill-card {
        display: grid;
        grid-template-columns: 1fr 1fr;   
    }
}

@media (min-width:519px) and (max-width: 694px) {
    .skill-card {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        
    }
}

@media (min-width: 695px) {
    .skill-card {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
}

@media (min-width: 770px) {
    .skill-card {
        display: flex;
    }
}